<template>
  <div class="details">
    <h2>{{msg}}</h2>
    <button @click="handleBack">返回</button>
  </div>
</template>


<script>
    export default{
        data(){
            return{
                msg:""
            }
        },
        created(){
          console.log(this.$route);
          console.log(this.$router);
          this.msg = this.$route.query.id
        },
      methods:{
        handleBack(){
            this.$router.back()
        }
      },
      beforeRouteUpdate(to,from,next){
        console.log("哈哈");
      },
      beforeRouteEnter(to,from,next){
          next()
//        console.log(to);
      },
      beforeRouteLeave(to,from,next){
          next()
      },
      watch : {
        '$route'(to,from,next){
          console.log(to);
          this.msg = to.query.id
        }
      },
//      activated(){
//        console.log("活跃状态");
//      },
//      deactivated(){
//        console.log("缓存状态");
//      }
    }
</script>


<style scoped>

</style>



