import Vue from 'vue'
import Router from 'vue-router'
// import Home from '@/components/home'
import De from '@/components/details'
import find from '@/components/find'

const home = ()=>import('@/components/home')
//(resolve)=>require(['../components/home'],resolve)
// const home = (resolve)=>require(['../components/home'],resolve)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/home'
    },
    {
      path:'/home',
      name:'home',
      component:home
    },
    {
      path:'/de',
      name:'de',
      component:De,
      meta :{ title: "购物车"}
    },
    {
      path:'/find',
      name:'find',
      component:find,
      meta :{ title: "发现"}
    }
  ]
})



